




























import uniPopup from '@/components/uni-popup/uni-popup.vue';

export default {
	components: {
		uniPopup
	},
	data() {
		return {
			popup: {
				position: 'bottom',
				noBotton: false,
				hidden: true,
			},
		};
	},
	methods: {
		popupClick(post){
			const vm = this;
			vm.popup.hidden = false;
			vm.popup.position = post;
		},
		confirm(){
			const vm = this;
			vm.popup.hidden = true;
		},
		cancel(){
			const vm = this;
			vm.popup.hidden = true;
		}
	},
}
