



































export default {
	props: {
		popupStyle: null,
		disable: {
			type: Boolean,
			default: false
		},
		hidden: {
			type: Boolean,
			default: true
		},
		position: {
			type: String,
			default: 'right'
		},
		noCancel: {
			type: Boolean,
			default: false
		},
		noBotton: {
			type: Boolean,
			default: false
		},
		cancelStyle: null,
		confirmStyle: null,
		title: null,
		cancelText: {
			type: String,
			default: '取消'
		},
		confirmText: {
			type: String,
			default: '确定'
		}
	},
	data() {
		return {
			pageCloak: false,		// 小程序端初始化时，组件闪烁
			hide: this.hidden
		};
	},
	watch: {
		hidden(nVal, oVal){
			this.hide = nVal
		},
		hide(nVal, oVal){
			this.$emit('update:hidden', nVal)
		}
	},
	mounted() {
		this.pageCloak = true
	},
	methods: {
		tagModal(e){
			this.hide = !this.hide;
			this.$emit('cancel', e)
		},
		cancel(e){
			// this.tagModal()
			// 内部关闭弹窗
			this.$emit('cancel', e)
		},
		confirm(e){
			// this.tagModal()
			// 内部关闭弹窗
			this.$emit('confirm', e)
		}
	}
}
